<template>
  <v-layout row wrap>
    <v-flex xs12>
      <v-parallax src="https://vuetifyjs.com/static/doc-images/parallax/material.jpg">
        <div class="text-xs-center">
          <h1>Claim satoshi online</h1>
          <v-text-field
            label="BTC Address"
            v-model="btcAddress"
          />
          <v-text-field
            label="Code"
            v-model="token"
          />
          <v-btn color="success" @click="claim()" :loading="isClaiming">Claim</v-btn>
          <br />
          <v-btn color="red" dark @click="getToken()" :loading="isGetting">Get the code</v-btn>
        </div>
      </v-parallax>
    </v-flex>
    <v-snackbar
      :timeout="3000"
      v-model="snackbar"
      top
    >
      {{snackbarText}}
      <v-btn flat color="pink" @click.native="snackbar = false">Close</v-btn>
    </v-snackbar>
  </v-layout>
</template>

<script>
export default {
  data: () => ({
    btcAddress: '',
    token: '',
    snackbar: false,
    snackbarText: '',
    isGetting: false,
    isClaiming: false
  }),
  methods: {
    getToken () {
      this.isGetting = true
      axios.get(`/satoshi/get_token`).then((res) => {
        let result = res.data
        let baseUrl = location.origin
        let url = baseUrl + '/code/' + result.message.token
        axios.get(
          `https://cors.io/?http://123link.co/api?api=6a8454f30f7e25720abe323f64fdd4a722e02261&url=${url}`)
          .then((res) => {
            this.snackbar = true
            this.isGetting = false
            if (res.data.status === 'success') {
              this.snackbarText = 'Success'
              location.href = res.data.shortenedUrl
            } else {
              this.snackbarText = 'Error. Please try again'
            }
        })
      })
    },
    claim () {
      this.isClaiming = true
      axios.get(`/wallet/claim?address=${this.btcAddress}&token=${this.token}`).then((res) => {
        this.isClaiming = false
        let result = res.data
        if (result.success) {
          this.snackbar = true
          this.snackbarText = `Success. You just received ${result.message} satoshis`
        } else {
          this.snackbar = true
          this.snackbarText = result.message
        }
      })
    }
  }
}
</script>

